:root{--primary-color: #3d054e;--text-color: #fff;--text-change: #000;--music-color: linear-gradient(117deg,#31033e,#470e88 102%);--dark-color: #290335}@keyframes FadeIn{from{opacity:0;transform:translateX(300px)}to{opacity:1;transform:translateX(0)}}@keyframes fadeDown{from{transform:translateY(-800px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes fadeUp{from{transform:translateY(800px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes HiddenDown{from{transform:translateY(0);opacity:0}to{transform:translateY(800px);opacity:1}}@keyframes fadeSlide{from{transform:translateX(-300px) skewX(-30deg)}to{transform:translateX(0) skewX(-30deg)}}@keyframes fadeLeft{from{opacity:0;transform:translateX(-250px)}to{opacity:1;transform:translateX(0)}}@keyframes fadeRight{from{opacity:0;transform:translateX(200px)}to{opacity:1;transform:translateX(0)}}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.song-mv-item{width:100%;background-color:transparent;text-align:center;border-radius:4px;box-shadow:4px 20px 40px rgba(0,0,0,.6);cursor:pointer}.song-mv-item .song-mv-thumbnail{position:relative;width:100%;height:auto;overflow:hidden}.song-mv-item .song-mv-thumbnail.broadcast{cursor:default}.song-mv-item .song-mv-thumbnail.broadcast i{display:block;text-shadow:0 0 4px var(--text-change);color:var(--text-color)}.song-mv-item .song-mv-thumbnail.broadcast img{opacity:.7}.song-mv-item .song-mv-thumbnail.broadcast:hover img{transform:scale(1);transition:all .5s;opacity:.7}.song-mv-item .song-mv-thumbnail:hover img{transform:scale(1.15);transition:all .5s;opacity:.7}.song-mv-item .song-mv-thumbnail:hover i{display:block}.song-mv-item .song-mv-thumbnail img{width:100%;height:auto;aspect-ratio:16/9;object-fit:cover;border-radius:4px 4px 0 0;transition:all .5s ease-in-out}.song-mv-item .song-mv-thumbnail i{position:absolute;font-size:3rem;color:#fff;top:calc(50% - 1.5rem);left:calc(50% - 1.5rem);text-shadow:0 0 4px var(--text-change);display:none}.song-mv-item .song-mv-thumbnail .song-mv-time{position:absolute;bottom:6px;right:4px;background-color:rgba(0,0,0,.7);padding:4px .5rem;color:#fff;font-size:.75rem;border-radius:3px}.song-mv-item .song-mv-thumbnail .song-mv-highlight{position:absolute;top:0;right:0;padding:4px .5rem;color:var(--text-color);font-size:.85rem;border-radius:0 4px 0 0}.song-mv-item .song-mv-thumbnail .song-mv-highlight.green{background-color:#00a42c}.song-mv-item .song-mv-thumbnail .song-mv-highlight.orange{background-color:#c24400}.song-mv-item .song-mv-body{width:100%;display:flex;align-items:center;height:72px}.song-mv-item .song-mv-body img{height:72%;width:auto;border-radius:50%;margin:0 .5rem}.song-mv-item .song-mv-body .song-mv-decoration{height:72%;float:left;margin-left:4px;padding:6px 0}.song-mv-item .song-mv-body .song-mv-decoration h3{font-size:.875rem;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;color:var(--text-color);font-weight:600;width:100%;text-align:left;margin:2px 0}.song-mv-item .song-mv-body .song-mv-decoration p{width:100%;font-size:.75rem;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;font-weight:400;color:var(--text-color);text-align:left;margin:2px 0}.song-mv-item .song-mv-body .song-mv-decoration p::before{content:"MV · "}