:root{--primary-color: #3d054e;--text-color: #fff;--text-change: #000;--music-color: linear-gradient(117deg,#31033e,#470e88 102%);--dark-color: #290335}@keyframes FadeIn{from{opacity:0;transform:translateX(300px)}to{opacity:1;transform:translateX(0)}}@keyframes fadeDown{from{transform:translateY(-800px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes fadeUp{from{transform:translateY(800px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes HiddenDown{from{transform:translateY(0);opacity:0}to{transform:translateY(800px);opacity:1}}@keyframes fadeSlide{from{transform:translateX(-300px) skewX(-30deg)}to{transform:translateX(0) skewX(-30deg)}}@keyframes fadeLeft{from{opacity:0;transform:translateX(-250px)}to{opacity:1;transform:translateX(0)}}@keyframes fadeRight{from{opacity:0;transform:translateX(200px)}to{opacity:1;transform:translateX(0)}}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.header-virtual{position:relative;height:92%;display:flex}.header-virtual .song-header-nav{width:80px;height:100%;text-align:center;padding:.35rem 0}.header-virtual .song-header-weather{position:relative;font-size:2.25rem;width:100%;height:100%}.header-virtual .song-header-search{position:relative;margin:auto 1.25rem}.header-virtual .song-header-search i{font-size:1rem;color:var(--primary-color);z-index:1}.header-virtual .song-header-search .song-search-icon{position:absolute;top:8px;left:16px;-webkit-text-stroke:1px}.header-virtual .song-header-search .song-delete-icon{position:absolute;top:8px;right:16px;cursor:pointer}.header-virtual .song-header-search input{width:400px;min-width:320px;padding:10px 48px;outline:none;border:none;border-radius:40px;font-size:.85rem;color:#555;font-weight:540}.header-virtual .search-song-box{position:absolute;top:calc(100% + 10px);right:0;width:calc(100% - 40px);max-height:500px;overflow:auto;overscroll-behavior:contain;padding:12px 8px;background-color:#fff;border-radius:8px;border:1px solid #ccc}.header-virtual .search-song-box .search-song-no-result{color:#555;font-size:1rem;padding:4px 0}.header-virtual .search-song-box .search-song-no-result i{font-size:.85rem;color:red;-webkit-text-stroke:1px;background-color:#f1d0d6;padding:8px;border-radius:50%;margin:0 .5rem}.header-virtual .search-song-list{width:100%;padding:6px 0 12px 0;border-bottom:1px solid #aaa6a6}.header-virtual .search-song-list h3{color:#000;font-size:1.5rem;font-weight:600;padding:4px 0}.header-virtual .search-song-item{width:100%;display:flex;align-items:center;padding:0 8px;border-radius:4px}.header-virtual .search-song-item:hover{cursor:pointer;background-color:rgba(0,0,0,.3)}.header-virtual .search-song-item .search-song-photo{height:3rem;width:auto;aspect-ratio:1/1;object-fit:cover;border-radius:4px;margin-right:.5rem}.header-virtual .search-song-item .search-song-decoration{height:84%;margin:auto 0;float:left;padding:6px 0}.header-virtual .search-song-item .search-song-decoration h3{font-size:.875rem;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;color:#000;font-weight:600;width:100%;text-align:left;margin:2px 0}.header-virtual .search-song-item .search-song-decoration p{width:100%;font-size:.75rem;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow:hidden;font-weight:400;color:#000;text-align:left;margin:2px 0}.header-virtual .search-song-item .search-song-decoration p::before{content:"Song · "}